.scissors {
  width: 200px;
  height: 200px;
  border: 20px solid hsl(39, 89%, 49%);
  border-image-slice: 1;
  border-radius: 50%;
  background-image: url(../../helpers/img/icon-scissors.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 70px;
  -webkit-box-shadow: 0 8px 0px 1px hsla(39, 89%, 49%, 0.6);
  -moz-box-shadow: 0 8px 0px 1px hsla(39, 89%, 49%, 0.6);
  box-shadow: 0 8px 0px 1px hsla(39, 89%, 49%, 0.6);
}

.scissors:hover {
  border-color: hsl(40, 84%, 53%);
}

.active-scissors {
  width: 300px;
  height: 300px;
  border: 35px solid hsl(39, 89%, 49%);
}

.winner- {
  -webkit-box-shadow: 1px 0px 1px 40px hsla(39, 89%, 49%, 0.6);
  -moz-box-shadow: 1px 0px 1px 40px hsla(39, 89%, 49%, 0.6);
  box-shadow: 0 8px 0px 1px hsla(39, 89%, 49%, 0.6),
    1px 0px 1px 40px hsla(214, 47%, 23%, 0.9),
    1px 0px 1px 80px hsla(214, 47%, 23%, 0.6),
    1px 0px 1px 130px hsla(214, 47%, 23%, 0.4);
}

@media screen and (min-width: 320px) and (max-width: 649px) {
  .scissors,
  .active-scissors {
    width: 130px;
    height: 130px;
    border: 15px solid hsl(39, 89%, 49%);
    box-shadow: 0 6px 0px 1px hsla(39, 89%, 49%, 0.6);
    background-size: 40%;
  }

  .winner-scissors {
    -webkit-box-shadow: 1px 0px 1px 15px hsla(39, 89%, 49%, 0.6);
    -moz-box-shadow: 1px 0px 1px 30px hsla(39, 89%, 49%, 0.6);
    box-shadow: 0 8px 0px 1px hsla(39, 89%, 49%, 0.6),
      1px 0px 1px 15px hsla(214, 47%, 23%, 0.9),
      1px 0px 1px 30px hsla(214, 47%, 23%, 0.6),
      1px 0px 1px 45px hsla(214, 47%, 23%, 0.4);
  }
}
